import Swiper from 'swiper';

const innerSlider = document.querySelector('.inner-slider__wrapper');
let photosSliderBtnNext = document.querySelector('.inner-slider__btn--next');
let photosSliderBtnPrev = document.querySelector('.inner-slider__btn--prev');

const photosSlider = () => {
  if (!innerSlider) {
    return;
  }

  let swiper = new Swiper(innerSlider, {
    spaceBetween: 20,
    speed: 300,

    navigation: {
      nextEl: photosSliderBtnNext,
      prevEl: photosSliderBtnPrev,
    },
  });

  photosSliderBtnPrev.addEventListener('click', function (e) {
    e.preventDefault();
    swiper.slidePrev();
  });

  photosSliderBtnNext.addEventListener('click', function (e) {
    e.preventDefault();
    swiper.slideNext();
  });

  window.onload = function () {
    swiper.update();
  };

  window.addEventListener('onorientationchange', function () {
    swiper.update();
  });

};

export default photosSlider;
