const orderSpoiler = () => {
  const spoilerToggle = document.querySelectorAll('.my-orders__accordion-btn');

  function closeAllSubmenu() {
    for (let j = 0; j < spoilerToggle.length; j++) {
      let spoilerContent = spoilerToggle[j].nextElementSibling;
      let textBeforeContent = spoilerToggle[j].previousElementSibling;

      spoilerContent.classList.remove('my-orders__item-content--opened');
      spoilerToggle[j].classList.remove('my-orders__accordion-btn--active');
      textBeforeContent.classList.remove('my-orders__item-text--active');
    }
  }

  for (let g = 0; g < spoilerToggle.length; g++) {
    spoilerToggle[g].addEventListener('click', function () {
      let spoilerContent = this.nextElementSibling;
      let textBeforeContent = this.previousElementSibling;

      if (spoilerContent.classList.contains('my-orders__item-content--opened')) {
        spoilerContent.classList.remove('my-orders__item-content--opened');
        this.classList.remove('my-orders__accordion-btn--active');
        textBeforeContent.classList.remove('my-orders__item-text--active');

        return false;
      }

      closeAllSubmenu();

      spoilerContent.classList.add('my-orders__item-content--opened');
      this.classList.add('my-orders__accordion-btn--active');
      textBeforeContent.classList.add('my-orders__item-text--active');

      return true;
    });
  }
};

export default orderSpoiler;
