const formInput = () => {
  const paymentInputs = document.querySelectorAll('#payment-form input');
  const paymentTextareas = document.querySelectorAll('#payment-form textarea');

  const addActiveState = (item) => {
    item.parentElement.querySelector('label').classList.add('active');
  };

  const removeActiveState = (item) => {
    if (!item.value) {
      item.parentElement.querySelector('label').classList.remove('active');
    }
  };

  const onFocusInputField = (items) => {
    items.forEach((item) => {
      item.addEventListener('focus', function () {
        addActiveState(item);
      });
    });

    items.forEach((item) => {
      item.addEventListener('blur', function () {
        removeActiveState(item);
      });
    });
  };

  onFocusInputField(paymentInputs);
  onFocusInputField(paymentTextareas);
};

export default formInput;
