const sidebar = () => {
  const sidebarOpenBtn = document.querySelectorAll('.sidebar-toggle');
  const sidebarCloseBtn = document.querySelectorAll('.sidebar__close-btn');
  const sidebarBackBtn = document.querySelectorAll('.sidebar__back-btn');
  const sidebarList = document.querySelectorAll('.sidebar');
  const overlay = document.querySelector('.overlay');
  const body = document.querySelector('body');

  const ESC_KEYCODE = 27;

  function checkMediaQuery() {
    if (window.innerWidth > 1023) {
      closeAllSidebars();
    }
  }

  window.addEventListener('resize', checkMediaQuery);

  function closeAllSidebars() {
    for (let i = 0; i < sidebarList.length; i++) {
      sidebarList[i].classList.remove('sidebar--active');
    }

    overlay.classList.remove('overlay--active');
    document.removeEventListener('keydown', onEscPressSidebarHide);
    overlay.removeEventListener('click', closeAllSidebars);
    body.classList.remove('body-no-scroll');
  }

  function onEscPressSidebarHide(evt) {
    if (evt.keyCode === ESC_KEYCODE) {
      closeAllSidebars();
    }
  }

  function onClickSidebarShow(evt) {
    evt.preventDefault();
    let modalId = this.getAttribute('data-modal');
    let modalElem = document.querySelector('.sidebar[data-modal=\'' + modalId + '\']');

    modalElem.classList.add('sidebar--active');
    overlay.classList.add('overlay--active');
    body.classList.add('body-no-scroll');

    document.addEventListener('keydown', onEscPressSidebarHide);
    overlay.addEventListener('click', closeAllSidebars);
  }

  function onClickReturnToPrevPage(evt) {
    evt.preventDefault();
    let parentSidebar = this.closest('.sidebar');

    parentSidebar.classList.remove('sidebar--active');
  }

  function addEventActionForSidebar(elems, action) {
    elems.forEach((item) => item.addEventListener('click', action));
  }

  addEventActionForSidebar(sidebarOpenBtn, onClickSidebarShow);
  addEventActionForSidebar(sidebarCloseBtn, closeAllSidebars);
  addEventActionForSidebar(sidebarBackBtn, onClickReturnToPrevPage);
};

export default sidebar;
