const validateForm = () => {
  const TEL_LENGTH = 11;

  const userNameRegExp = /^[A-Za-zА-Яа-яЁё]*\s*$/;
  const userEmailRegExp = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;

  const formFeedback = document.querySelector('.feedback__form');

  if (formFeedback) {
    const fields = formFeedback.querySelectorAll('.field');

    const userName = formFeedback.querySelector('input[type = "text"]');
    const userTel = formFeedback.querySelector('input[type = "tel"]');
    const userEmail = formFeedback.querySelector('input[type = "email"]');
    const userText = formFeedback.querySelector('textarea');

    const agreementField = formFeedback.querySelector('#feedback-agreement');

    const createErrorBlock = (field) => {
      let errorBlock = field.parentElement.querySelector('.feedback__message-error--field');
      errorBlock.classList.add('error-show');
    };


    const removeErrorBlock = (field) => {
      let errorBlock = field.parentElement.querySelector('.error-show');
      if (errorBlock) {
        errorBlock.classList.remove('error-show');
      }
    };


    const createEmptyBlock = (field) => {
      let errorEmptyBlock = field.parentElement.querySelector('.feedback__message-error--empty');
      errorEmptyBlock.classList.add('error-show');
    };

    const showFieldError = (field) => {
      field.classList.add('field-error');
      createErrorBlock(field);
    };

    const removeFieldError = (field) => {
      field.classList.remove('field-error');
    };

    const removeValidationError = () => {
      const errors = formFeedback.querySelectorAll('.feedback__message-error--field');
      fields.forEach((field) => {
        removeFieldError(field);
      });
      errors.forEach((error) => {
        error.classList.remove('error-show');
      });
    };

    const removeValidationEmpty = () => {
      const errorsEmpty = formFeedback.querySelectorAll('.feedback__message-error--empty');
      fields.forEach((field) => {
        removeFieldError(field);
      });
      errorsEmpty.forEach((error) => {
        error.classList.remove('error-show');
      });
    };

    const showFieldsPrompt = (field) => {
      let promptBlock = field.parentElement.querySelector('.feedback__message-prompt');
      promptBlock.classList.add('prompt-show');
      removeValidationError();
      removeValidationEmpty();
    };

    const removePrompts = () => {
      const prompts = formFeedback.querySelectorAll('.prompt-show');
      prompts.forEach((prompt) => {
        prompt.classList.remove('prompt-show');
      });
    };

    const showLabel = (field) => {
      field.parentElement.querySelector('.feedback__label').classList.add('label-active');
    };

    const removeLabel = (field) => {
      if (field.value === '') {
        field.parentElement.querySelector('.feedback__label').classList.remove('label-active');
      }
    };

    const showFieldsEdited = (field) => {
      field.classList.add('field-edited');
    };

    const removeFieldsEdited = (field) => {
      const fieldEdited = field.parentElement.querySelector('.field-edited');
      if (fieldEdited) {
        field.classList.remove('field-edited');
      }
    };

    let validationForm = true;

    const checkFieldsPresence = () => {
      removeValidationError();
      removeValidationEmpty();
      fields.forEach((field) => {
        if (!field.value) {
          removeErrorBlock(field);
          createEmptyBlock(field);
          showFieldError(field);
          validationForm = false;
        }
      });
    };

    const validateValue = (regExp, value) => {
      return regExp.test(value);
    };

    let validationName = true;

    const checkNameMatch = () => {
      showLabel(userName);
      removeErrorBlock(userName);
      if (!validateValue(userNameRegExp, userName.value) && (userName.value !== '')) {
        removeFieldsEdited(userName);
        createErrorBlock(userName);
        showFieldError(userName);
        validationName = false;
      } else if (validateValue(userNameRegExp, userName.value) && (userName.value !== '')) {
        showFieldsEdited(userName);
        validationName = true;
      } else {
        validationName = false;
        removeLabel(userName);
      }
    };

    let validationTel = true;

    const checkTelMatch = () => {
      showLabel(userTel);
      removeErrorBlock(userTel);
      const telLength = userTel.value.replace(/\D+/g, '').length;
      if ((telLength < TEL_LENGTH) && (userTel.value !== '')) {
        removeFieldsEdited(userTel);
        createErrorBlock(userTel);
        showFieldError(userTel);
        validationTel = false;
      } else if ((telLength === TEL_LENGTH) && (userTel.value !== '')) {
        showFieldsEdited(userTel);
        validationTel = true;
      } else {
        validationTel = false;
        removeLabel(userTel);
      }
    };

    let validationEmail = true;

    const checkEmailMatch = () => {
      showLabel(userEmail);
      removeErrorBlock(userEmail);
      if (!validateValue(userEmailRegExp, userEmail.value) && (userEmail.value !== '')) {
        removeFieldsEdited(userEmail);
        createErrorBlock(userEmail);
        showFieldError(userEmail);
        validationEmail = false;
      } else if (validateValue(userEmailRegExp, userEmail.value) && (userEmail.value !== '')) {
        showFieldsEdited(userEmail);
        validationEmail = true;
      } else {
        validationEmail = false;
        removeLabel(userEmail);
      }
    };

    let validationText = true;

    const checkMaxValue = (field) => {
      showLabel(field);
      removeErrorBlock(field);
      let max = field.getAttribute('maxlength');
      if ((field.value.length > max) && (field.value !== '')) {
        removeFieldsEdited(field);
        createErrorBlock(field);
        showFieldError(field);
        validationText = false;
      } else if ((field.value.length <= max) && (field.value !== '')) {
        showFieldsEdited(field);
        validationText = true;
      } else {
        validationText = false;
        removeLabel(userText);
      }
    };

    const checkTextMatch = () => {
      checkMaxValue(userText);
    };

    const showNamePrompt = () => {
      showFieldsPrompt(userName);
      if (userName.value === '') {
        showLabel(userName);
      }
    };

    const showTelPrompt = () => {
      showFieldsPrompt(userTel);
      if (userTel.value === '') {
        showLabel(userTel);
      }
    };

    const showEmailPrompt = () => {
      showFieldsPrompt(userEmail);
      if (userEmail.value === '') {
        showLabel(userEmail);
      }
    };

    const showTextPrompt = () => {
      showFieldsPrompt(userText);
      if (userText.value === '') {
        showLabel(userText);
      }
    };

    const removeAgreementErrorBlock = () => {
      let agreementError = agreementField.parentElement.querySelector('.feedback__message-error');
      if (agreementError) {
        removeErrorBlock(agreementError);
      }
    };

    let validationAgreement = true;

    const checkAgreement = () => {
      if (!agreementField.checked) {
        agreementField.classList.add('feedback__agreement-invalid');
        createErrorBlock(agreementField);
        validationAgreement = false;
      } else {
        agreementField.classList.remove('feedback__agreement-invalid');
        removeAgreementErrorBlock();
        validationAgreement = true;
      }
    };

    userName.addEventListener('focus', showNamePrompt);
    userTel.addEventListener('focus', showTelPrompt);
    userEmail.addEventListener('focus', showEmailPrompt);
    userText.addEventListener('focus', showTextPrompt);

    fields.forEach((field) => {
      field.addEventListener('blur', () => {
        removePrompts();
        removeLabel(field);
      });
    });

    agreementField.addEventListener('change', () => {
      checkAgreement();
    });

    userText.addEventListener('keyup', () => {
      if (userText.scrollTop > 0) {
        userText.style.height = userText.scrollHeight + 'px';
      }
    });

    formFeedback.addEventListener('submit', (evt) => {
      evt.preventDefault();

      checkFieldsPresence();
      checkNameMatch();
      checkTelMatch();
      checkEmailMatch();
      checkTextMatch();
      checkAgreement();

      if (
        validationForm &&
        validationName &&
        validationTel &&
        validationEmail &&
        validationText &&
        validationAgreement
      ) {
        formFeedback.submit();
        formFeedback.reset();
      }
    });
  }
};

export default validateForm;
