const selectOption = () => {
  const selectBlock = document.querySelectorAll('.items-selected__select');

  if (selectBlock.length > 0) {
    let select = function () {
      selectBlock.forEach((block) => {
        const selectHeader = block.querySelectorAll('.items-selected__header');
        const selectItem = block.querySelectorAll('.items-selected__item');
        const selectCurrent = block.querySelector('.items-selected__current');
        const selectInput = block.querySelector('#my-select');

        selectHeader.forEach((item) => {
          item.addEventListener('click', selectToggle);
        });

        selectItem.forEach((item) => {
          item.addEventListener('click', selectChoose);
        });

        function selectToggle() {
          this.parentElement.classList.toggle('is-active');
        }

        function selectChoose() {
          let text = this.innerText;
          selectCurrent.innerText = text;
          block.classList.remove('is-active');
          selectInput.value = text;
        }
      });
    };
    select();
  }
};

export default selectOption;
