const tabs = () => {
  const tabTriggers = document.querySelectorAll('.tab-trigger');
  const tabContents = document.querySelectorAll('.tab-content__item');

  if (tabTriggers.length) {
    tabTriggers[0].classList.add('tab-trigger--active');
  }

  const removeAllActiveTriggers = () => {
    tabTriggers.forEach((item) => {
      item.classList.remove('tab-trigger--active');
    });
  };

  const closeAllTabs = () => {
    tabContents.forEach((item) => {
      item.classList.remove('tab-content__item--active');
      removeAllActiveTriggers();
    });
  };

  tabTriggers.forEach(function (trigger) { // 1
    trigger.addEventListener('click', function (evt) { // 2
      evt.preventDefault();
      const id = trigger.getAttribute('href'); // 1
      const content = document.querySelector(id); // 2
      closeAllTabs();
      content.classList.add('tab-content__item--active');
      trigger.classList.add('tab-trigger--active');
    });
  });
};

export default tabs;
