
const map = () => {
  const mapContainer = document.querySelector('#map');
  const MOBILE_WIDTH = 600;

  let zoomValue = 9.2;

  if (window.innerWidth < MOBILE_WIDTH) {
    zoomValue = 8.5;
  }

  if (mapContainer) {
    const createMap = () => {
      const myMap = new ymaps.Map('map', {
        center: [55.8, 36.85], // Московская область, Одинцовский городской округ, поселок Горки-2, Рублево-Успенское шоссе, дом 22В
        zoom: zoomValue,
        controls: [],
      });

      myMap.controls.add('zoomControl', {
        size: 'small',
        float: 'none',
        position: {
          bottom: '320px',
          right: '30px',
        },
      });

      myMap.controls.add('geolocationControl', {
        size: 'small',
        float: 'none',
        position: {
          bottom: '250px',
          right: '30px',
        },
      });

      if (window.innerWidth < MOBILE_WIDTH) {
        myMap.controls.remove('zoomControl');
        myMap.controls.remove('geolocationControl');
      }
    };

    ymaps.ready(createMap);

  }

};

export default map;

