const recipeSwiper = () => {
  const breakpoint = window.matchMedia('(min-width:601px)');
  let recipeSlider;
  let slider = document.querySelector('.recipes-slider__container');
  let swipers = document.querySelectorAll('.recipes-slider__container');

  if (!slider) {
    return;
  }

  const breakpointChecker = () => {
    if (breakpoint.matches === false) {
      if (recipeSlider !== undefined) {
        recipeSlider.destroy(true, true);
      }
    } else if (breakpoint.matches === true) {
      enableSwiper();
    }
  };


  const enableSwiper = () => {
    recipeSlider = new Swiper('.recipes-slider__container', {
      slidesPerView: 'auto',
      loop: true,
      spaceBetween: 23,
      updateOnWindowResize: false,
      setWrapperSize: true,
      updateOnImagesReady: false,

      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },

      breakpoints: {
        960: {
          spaceBetween: 18,
        },
      },
    });
  };

  let initSlider = () => {
    swipers.forEach((element) => {
      let slides = element.querySelectorAll('.swiper-slide');

      slides.forEach((slide) => {
        slide.classList.add('swiper-slide-active');
        setTimeout(() => {
          slide.classList.remove('swiper-slide-active');
        }, 330);
      });

      setTimeout(() => {
        element.style.minHeight = `${element.clientHeight}px`;
      }, 310);
    });

    setTimeout(() => {
      breakpointChecker();
      breakpoint.addListener(breakpointChecker);
    }, 900);
  };

  window.addEventListener('load', () => {
    initSlider();
  });
};

export {recipeSwiper};

