const dropdown = () => {
  const dropdownToggleList = document.querySelectorAll('.dropdown__toggle');

  function closeAllDropdowns() {
    for (let i = 0; i < dropdownToggleList.length; i++) {
      let dropdownList = dropdownToggleList[i].nextElementSibling;
      dropdownList.classList.remove('dropdown__list--opened');
      dropdownToggleList[i].classList.remove('active');
      document.removeEventListener('click', dropEvent);
    }
  }

  function onClickToggleMenu(button) {
    button.addEventListener('click', function (evt) {
      let dropdownList = button.nextElementSibling;

      if (dropdownList.classList.contains('dropdown__list--opened') && button.contains(evt.target)) {
        dropdownList.classList.remove('dropdown__list--opened');
        button.classList.remove('active');
        document.removeEventListener('click', dropEvent);

        return;
      }

      closeAllDropdowns();

      dropdownList.classList.add('dropdown__list--opened');
      button.classList.add('active');
      document.addEventListener('click', dropEvent);

      return;
    });
  }

  for (let i = 0; i < dropdownToggleList.length; i++) {
    onClickToggleMenu(dropdownToggleList[i]);
  }

  function dropEvent(e) {
    for (let i = 0; i < dropdownToggleList.length; i++) {
      let button = dropdownToggleList[i];
      let dropdownList = button.nextElementSibling;

      if (e.target !== button
        && !button.contains(e.target)
        && e.target !== dropdownList
        && !dropdownList.contains(e.target)) {
        dropdownList.classList.remove('dropdown__list--opened');
        dropdownToggleList[i].classList.remove('active');
      }
    }
  }
};

export default dropdown;
