const subscribe = () => {
  const subscribeForm = document.querySelector('.form-subscribe');
  if (subscribeForm) {
    const subscribeButton = subscribeForm.querySelector('.form-subscribe__button');
    const emailInput = subscribeForm.querySelector('[type="email"]');
    const checkbox = subscribeForm.querySelector('[type="checkbox"]');

    subscribeButton.addEventListener('click', function (evt) {
      evt.preventDefault();
      if (subscribeForm.checkValidity()) {
        subscribeButton.classList.add('done');
        subscribeForm.classList.add('valid');
        subscribeForm.classList.remove('invalid');
        emailInput.classList.remove('invalid');
        checkbox.classList.remove('invalid');
        emailInput.value = '';
        setTimeout(() => subscribeForm.classList.remove('valid'), 1500);
      } else if (!emailInput.checkValidity() && !checkbox.checked){
        emailInput.classList.add('invalid');
        checkbox.classList.add('invalid');
        subscribeForm.classList.add('invalid');
      } else if (!emailInput.checkValidity()) {
        emailInput.classList.add('invalid');
        subscribeForm.classList.add('invalid');
        checkbox.classList.remove('invalid');
      } else if (!checkbox.checked) {
        emailInput.classList.remove('invalid');
        checkbox.classList.add('invalid');
        subscribeForm.classList.add('invalid');
      } 
    });
  }
};

export {subscribe};
