const submenu = () => {
  const submenuToggle = document.querySelectorAll('.submenu-toggle');

  function closeAllSubmenu() {
    for (let j = 0; j < submenuToggle.length; j++) {
      let menu = submenuToggle[j].nextElementSibling;
      menu.classList.remove('submenu--opened');
      submenuToggle[j].classList.remove('submenu-toggle--active');
    }
  }

  for (let g = 0; g < submenuToggle.length; g++) {
    submenuToggle[g].addEventListener('click', function () {
      let menu = this.nextElementSibling;

      if (menu.classList.contains('submenu--opened')) {
        menu.classList.remove('submenu--opened');
        this.classList.remove('submenu-toggle--active');

        return false;
      }

      closeAllSubmenu();

      menu.classList.add('submenu--opened');
      this.classList.add('submenu-toggle--active');

      return true;
    });
  }
};

export default submenu;
