const promoSwiper = () => {

  let promoSlider = document.querySelector('.promo');

  if (!promoSlider) {
    return;
  }

  let swiper = new Swiper(promoSlider, {
    slidesPerView: 1,
    spaceBetween: 350,
    loop: true,
    autoHeight: true,

    navigation: {
      nextEl: '.promo .navigation-arrow--next',
      prevEl: '.promo .navigation-arrow--prev',
    },
  });

  window.addEventListener('onorientationchange', function () {
    swiper.update();
  });

  window.onload = function () {
    swiper.update();
  };
};

export {promoSwiper};
