const marketSwiper = () => {
  const breakpoint = window.matchMedia('(min-width:961px)');
  let marketSlider;
  let slider = document.querySelector('.market-area__img-slider');

  if (!slider) {
    return;
  }

  const breakpointChecker = () => {
    if (breakpoint.matches === false) {
      if (marketSlider !== undefined) {
        marketSlider.destroy(true, true);
      }
    } else if (breakpoint.matches === true) {
      enableSwiper();
    }
  };

  const enableSwiper = () => {
    marketSlider = new Swiper('.market-area__img-slider', {
      slidesPerView: 'auto',
      spaceBetween: 19,
      direction: 'horizontal',
      loop: true,

      breakpoints: {
        1440: {
          spaceBetween: 19,
        },

        1024: {
          spaceBetween: 14,
        },
      },
    });
  };

  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
};

export {marketSwiper};
