const thumbnailsSwiper = () => {
  const createThumbnailsSwiper = () => {
    const galleryThumbs = new window.Swiper('.gallery-thumbs', {
      direction: 'vertical',
      slidesPerView: 4,
      slideToClickedSlide: true,
      breakpoints: {
        600: {
          slidesPerView: 'auto',
          spaceBetween: 18,
          direction: 'horizontal',
        },
        1024: {
          slidesPerView: 4,
          spaceBetween: 20,
        },
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });

    const galleryTop = new window.Swiper('.gallery-top', {
      spaceBetween: 0,
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
      thumbs: {
        swiper: galleryThumbs,
      },
    });
  };

  createThumbnailsSwiper();

  window.addEventListener('resize', function () {
    createThumbnailsSwiper();
  });
};

export default thumbnailsSwiper;
