const eventSwiper = () => {
  const breakpoint = window.matchMedia('(min-width:961px)');
  let eventSlider;
  let slider = document.querySelector('.cards-list-wrap');

  if (!slider) {
    return;
  }

  const breakpointChecker = () => {
    if (breakpoint.matches === false) {
      if (eventSlider !== undefined) {
        eventSlider.destroy(true, true);
      }
    } else if (breakpoint.matches === true) {
      enableSwiper();
    }
  };

  const enableSwiper = () => {
    eventSlider = new Swiper('.cards-list-wrap', {
      slidesPerView: 4,
      direction: 'horizontal',
      loop: true,
      spaceBetween: 16,

      breakpoints: {
        1440: {
          spaceBetween: 26,
        },

        1024: {
          spaceBetween: 16,
        },
      },
    });
  };

  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
};

export {eventSwiper};
