const formSearch = () => {
  const headerSearchFormInput = document.querySelector('#header-form-search input');
  const headerSearchFormBtn = document.querySelector('#header-form-search button[type=submit]');
  const sidebarSearchFormInput = document.querySelector('#sidebar-search-form input');
  const sidebarSearchFormBtn = document.querySelector('#sidebar-search-form button[type=submit]');

  function onSubmitSearchForm(input, btn) {
    if (btn) {
      btn.addEventListener('click', function () {
        if (input.validity.valid) {
          input.classList.remove('error');
        } else {
          input.classList.add('error');
        }
      });
    }

    if (input) {
      input.addEventListener('blur', function () {
        input.classList.remove('error');
      });

      input.addEventListener('input', function () {
        if (input.validity.valid) {
          input.classList.remove('error');
        } else {
          input.classList.add('error');
        }
      });
    }
  }

  onSubmitSearchForm(headerSearchFormInput, headerSearchFormBtn);
  onSubmitSearchForm(sidebarSearchFormInput, sidebarSearchFormBtn);
};

export default formSearch;
