const stepper = () => {
  const plusBtn = document.querySelectorAll('.stepper__btn--plus');
  const minusBtn = document.querySelectorAll('.stepper__btn--minus');
  const inputStepperList = document.querySelectorAll('.stepper input');

  inputStepperList.forEach((item) => {
    item.addEventListener('input', checkInput);
    item.addEventListener('focus', onFocusInput);
    item.addEventListener('blur', onBlurInput);
  });

  function checkInput() {
    if (this.value < 1) {
      this.value = '';
    }
  }

  function onFocusInput() {
    this.parentElement.classList.add('stepper--active');
  }

  function onBlurInput() {
    this.parentElement.classList.remove('stepper--active');

    if (this.value < 1 || !this.value) {
      this.value = 1;
    }
  }

  function plusStep() {
    let input = this.nextElementSibling;

    input.value++;
  }

  function minusStep() {
    let input = this.previousElementSibling;

    if (input.value <= 1) {
      input.value = 1;
    } else {
      input.value--;
    }
  }

  plusBtn.forEach((item) => {
    item.addEventListener('click', plusStep);
  });

  minusBtn.forEach((item) => {
    item.addEventListener('click', minusStep);
  });

};

export default stepper;
