const choiceSwiper = () => {
  const breakpoint = window.matchMedia('(min-width:961px)');
  let choiceSlider;
  let slider = document.querySelector('.page-sliders__container')

  if (!slider) {
    return;
  }

  const breakpointChecker = () => {
    if (breakpoint.matches === false) {
      if (choiceSlider !== undefined) {
        choiceSlider.destroy(true, true);
      }
    } else if (breakpoint.matches === true) {
      enableSwiper();
    }
  };

  const enableSwiper = () => {
    choiceSlider = new Swiper('.page-sliders__container', {
      slidesPerView: 6,
      spaceBetween: 17,
      direction: 'horizontal',
      loop: true,

      breakpoints: {
        1280: {
          slidesPerView: 6,
          spaceBetween: 17,
        },

        961: {
          slidesPerView: 4,
          spaceBetween: 16,
        },
      },

      navigation: {
        nextEl: '.choice .navigation-arrow--next',
        prevEl: '.choice .navigation-arrow--prev',
      },
    });

  };

  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
};

export {choiceSwiper};
