let sliderMarket = null;
const swiperContainer = document.querySelector('.inner-menu__wrapper');
const swiperWrapper = document.querySelector('.inner-menu__list');
const swiperSlides = document.querySelectorAll('.inner-menu__item');
const activateSlider = () => {
  if (swiperContainer !== null && swiperWrapper !== null && swiperSlides !== null) {
    const viewport = document.documentElement.clientWidth;
    if (viewport < 830 && !sliderMarket) {
      swiperContainer.classList.add('swiper-container');
      swiperWrapper.classList.add('swiper-wrapper');
      swiperSlides.forEach((swiperSlide) => {
        swiperSlide.classList.add('swiper-slide');
      });
      sliderMarket = new window.Swiper(swiperContainer, {
        spaceBetween: 20,
        slidesPerView: 'auto',
        breakpoints: {
          // when window width is >= 320px
          320: {
            spaceBetween: 10,
          },
        },
      });
    } else if (viewport >= 830 && sliderMarket) {
      sliderMarket.destroy();
      sliderMarket = null;
      swiperContainer.classList.remove('swiper-container');
      swiperWrapper.classList.remove('swiper-wrapper');
      swiperSlides.forEach((swiperSlide) => {
        swiperSlide.classList.remove('swiper-slide');
      });
    }
  }
};

window.addEventListener('resize', function () {
  activateSlider();
});

export default activateSlider;
